import React, {Component} from 'react';


const Checks = (props) => {
let checks=props.checks;

let checkViewUser = document.querySelector('.formCheck');
console.log(checkViewUser);
if(!props.checkClickSelector){
    checkViewUser.hidden=false;
}


if(!!props.checkClickCreateCompany) {
    checkViewUser.hidden=true;
}
//Добавление нового чека
function addCheck(e) {
    e.preventDefault();
    let product = document.querySelector('.addItemChecks');
    let btnCreate = document.querySelector('.btnCreate');
    product.value = 1;
    
    btnCreate.click();

    




}

    //добавелине чеков по количеству
    function addItemChecks(e) {
        e.preventDefault();
        let item = document.querySelector('.addItemChecks').value;
        for(let i = 0; i<item; i++) {
            addCheck(e)
        }
        //addCheck()
    }

     
//Добавление новой позиции в чек
function addProduct(e) {
    e.preventDefault();

    let product = document.querySelector('.productReceipt');
    let blockProduct = document.createElement('div');

    product.innerHTML += `
    <div class="productReceipt-item">
                    
                    <input id="priceReceipt" type="text" class="form-control col-2 offset-md-2" placeholder="Цена"/>
                    <input id="nameReceipt" type="text" class="form-control col-6" placeholder="Наименование товара"/>
                    <input id="quantityReceipt" type="text" class="form-control" value='1'  placeholder="Количество"/>
                </div>
    `;
    
   // product.append(blockProduct);
    
}

        return(
            <div>



<form hidden={true} className='formCheck'  >
-----------------------------------Чеки------------------------------------------
<input id="addItemChecks" type="text"  className="addItemChecks form-control" placeholder="Количество чеков"/>
<button className="btn btn-primary btnCreate" onClick={props.addChecks}  type="button">Создать</button>
<br/>
-----------------------------------Список------------------------------------------
<div className="form-row">
  
      <div className="checkItem">
    
        
        {checks.map( (item, index) => {

            return <div key={index} index={index} className="productReceipt col-24"> <div>{index+1}</div> 
            <input id="date" type="date" onChange={props.changeCompany} value={item.date}  className="form-control" placeholder="Дата"/>
            <input id="price" type="text" onChange={props.changeCompany} value={item.price}  className="form-control col-2" placeholder="Цена"/>
               <input id="name" type="text" onChange={props.changeCompany} value={item.name} className="form-control col-6" placeholder="Наименование товара"/>
               <input id="item" type="text" onChange={props.changeCompany} value={item.item} className="form-control col-1"  placeholder="Количество"/>
               <button id='btnDellCheck' onClick={props.dellCheckItem} className="btn btn-primary btnDellCheck">Удалить</button>


{/* 
            {(item.product).map((product, i) => {
               return <div className="productReceipt-item" index={i}  key={i}><input id="price" type="text" onChange={props.changeCompany} value={product.price}  className="form-control col-2" placeholder="Цена"/>
               <input id="name" type="text" onChange={props.changeCompany} value={product.name} className="form-control col-6" placeholder="Наименование товара"/>
               <input id="item" type="text" onChange={props.changeCompany} value={product.item} className="form-control"  placeholder="Количество"/>
               </div>
            })} */}
            </div>
            
          
            
                   
        })}
        
            {/* <input id="date" type="text" onChange={props.changeCompany} value={props.checks[0].date}  className="form-control" placeholder="Дата"/> */}
            {/* <input id="price" type="text" onChange={props.changeCompany} value={props.checks[0].product[0].price}  className="form-control col-2" placeholder="Цена"/> */}
            {/* <input id="name" type="text" onChange={props.changeCompany} value={props.checks[0].product[0].name} className="form-control col-6" placeholder="Наименование товара"/> */}
            {/* <input id="item" type="text" onChange={props.changeCompany} value={props.checks[0].product[0].item} className="form-control"  placeholder="Количество"/><input id="item" type="text" onChange={props.changeCompany} value={props.checks[0].product[0].item} className="form-control"  placeholder="Количество"/> */}
            
    
    </div>
    
</div>

{/* <button className="btn btn-primary" onClick={addProduct}  type="button">Добавить товар</button> */}
    <button className="btn btn-primary" onClick={addCheck}  type="button">Добавить чек</button>
    <button className="btn btn-primary" onClick={props.saveChange}  type="button">Сохранить</button>
</form>
            </div>
        );
        
    

};


export default Checks;