// получаем ID выбранной компании
changeCompanyArr(e) {
  let formCompany = document.querySelector('.formCompany');
  
  this.setState({
    checkClickSelector: false
  })
  let saveChange = document.querySelector('.saveChange');
    saveChange.hidden = true;

  let buttAddCompany = document.querySelector('.buttAddCompany');
        buttAddCompany.hidden = true;



  let companyIndex = e.target.selectedIndex;
  let companyItem = this.state.companyArr[companyIndex][1];
  
  if (companyIndex===0) {
    formCompany.hidden=true;
    this.setState({
      company: delInputTextCompany,
      checkClickCreateCompany: true
     

    })
    return
  } 


  //Выводим кнопку Удалить
  let deleteCompnyButton = document.querySelector('.deleteCompnyButton');
  deleteCompnyButton.hidden=false;

  

  //выводим инпуты с данными компании
  formCompany.hidden=false;
  

  fetch('/users/usersId'+ companyItem)
  .then(res => res.json())
  .then(users => {
    
    this.setState({
      checkClickCreateCompany: false,
      company:users
    })
  }

);


}

//удаление компании
deleteCompany(e) {
  e.preventDefault();
 // eslint-disable-next-line no-restricted-globals
  let questionDelete = confirm('Вы действитеьно хотите удалить компанию?');
  
  if(!questionDelete) return;
 

  let companyId = this.state.company.id;



  fetch('/users/deleteCompany' + companyId, {
    method: 'DELETE',
  })
  .then(res => res.json()) // or res.json()
  .then(users => {
    companyArr = [['Выберите компанию', '']];
          users.map((item) => {
            
            companyArr.push([item.nameCompany, item.id]);

            
          })
          
//получаем данные и отправляем State наименования компаний


          this.setState({
            companyArr,
            company: delInputTextCompany,
            checkClickCreateCompany: true
          })
          

  }
    )

    let formCompany = document.querySelector('.formCompany').hidden=true;

  
}

//Изменение данных компании
saveChange(e) {
  e.preventDefault();
  let companyId = this.state.company.id;
  let data = this.state.company;
  

  fetch('/users/putCompany' + companyId, {
    method: 'PUT',
    mode: 'cors', // no-cors, *cors, same-origin
    cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
    credentials: 'same-origin', // include, *same-origin, omit
    headers: {
      'Content-Type': 'application/json'
      // 'Content-Type': 'application/x-www-form-urlencoded',
    },
    redirect: 'follow', // manual, *follow, error
    referrerPolicy: 'no-referrer', // no-referrer, *client
    body: JSON.stringify(data) // body data type must match "Content-Type" header
  })
  .then(res => res.json()) // or res.json()
  .then(users => {
    
          console.log(users)

          this.setState({
            company: users
          })

            
          })
          
}
//Кнопка Создать компанию, Читсим все поля для добавления компании
createCompany() {
  let buttAddCompany = document.querySelector('.buttAddCompany');
  let formCompany = document.querySelector('.formCompany');
  let deleteCompnyButton = document.querySelector('.deleteCompnyButton');
  let companyArr = document.querySelector('.companyArr');
  let saveChange = document.querySelector('.saveChange').hidden=true;
  companyArr.selectedIndex = 0;


  deleteCompnyButton.hidden=true;
  formCompany.hidden=false;
        buttAddCompany.hidden = false;
        

        this.setState({
          company: delInputTextCompany,
          checkClickSelector: true,
          checkClickCreateCompany: true,
        })


        
}


addChecks(e){
  e.preventDefault();
 let checkItem = e.target.previousElementSibling.value;
  let checkNull =  {
    date: '',
    
        price: null,
        name: '',
        item: '1',
      
      
    
  
}

let company = {...this.state.company}

for(let i=1; i<=checkItem;i++){
  company.checks.push(checkNull);
}

this.setState({
  company
})

this.saveChange(e);

}


dellCheckItem(e) {
  e.preventDefault();
  let company = this.state.company;
  let i =e.target.closest('.productReceipt').getAttribute("index");

  company.checks.splice(i,1);
  document.querySelector('.saveChange').hidden=false;
  this.setState({
    company
  })
  console.log(e.target)
}





