import React, {Component} from 'react';
import ChecksViewAdd from '../../blocks/ChecksViewAdd/ChecksViewAdd'
import './ChecksView.css';



export default class ChecksView extends Component {
    state = {
        company: null
    };


   //при загрузке страницы получаем данные из базы
  componentDidMount() {
    fetch('/users/companyGet')
      .then(res => res.json())
      .then(company => {
          
        for (let key in company) {
            console.log(company[key]);
        }
        this.setState({
          company
        })

        console.log(company);
      }
        
);
  }
    
    render() {

        
        return(
            <div className="checkViews">

            
               {

                   !this.state.company ? 
                   'Подождите'
                   :
                   
                    <ChecksViewAdd
                        checksLength = {this.state.company.checks.length}
                        company = {this.state.company}
                    />
               }
            </div>
        )
    }
}