      //вычисляем смену !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
       function changeCheck(date) {
       
        if (date == checkMin) {
          changeSmena = changeCheckMin;
          return changeSmena;
        }else {


         
          console.log(checkMinItem)

          let dateCheckItem = new Date(date);
          let dateCheckOne = new Date(checkMin)
          let daysItemTest = +(dateCheckItem - dateCheckOne) / (1000 * 3600 * 24);
          
          
//отнимает по одному дню
          function getDateAgo(date, days) {
            let dateCopy = new Date(date);
            let item= daysItemTest;
            for(let i=0; i<daysItemTest; i++){
              
              if(dateCopy.getDay() == 6 || dateCopy.getDay() == '0') {
                item-=1;
              }
             // console.log(dateCopy)
              dateCopy.setDate(dateCopy.getDate() - days);
            }
           
            
            return item;
          }
          
          
          
          console.log(getDateAgo(dateCheckItem, 1)); // 1, (1 Jan 2015)
          

          //let daysItem = dateCheckOne.getDay();
          changeSmena = getDateAgo(dateCheckItem, 1) + changeCheckMin;
          console.log(daysItemTest)

          return changeSmena;
          //return changeCheckMin + daysItem



        }
        
         //console.log(checkMin, new Date(checkArr[0]))

       }



                  //формируем время рандом между двумя числами
                  function randomInteger(min, max) {
                    // случайное число от min до (max+1)
                    let rand = min + Math.random() * (max + 1 - min);
                    return Math.floor(rand);
                }
    
       


       // формирование даты и времени
       function dateCompany(item) {
            //формируем дату
            let date = item.split('-').reverse().join('.').split('');
            date.splice(6,2).join('')
            date = date.join('')

    
              
             let hours = randomInteger(10, 17);
             let minutes = randomInteger(10, 59);
            let time = [hours, minutes].join(':')



            let hoursMinutes = [hours, minutes].join('');
          checkNumItem(+hoursMinutes);

           
            
            

            return [date, time].join(' ');

       }

//!!!!!!!!!!!!__________=============КАК РЕАЛИЗОВАТЬ МНОЖЕСТВО ЧЕКОВ В ОДИНЬ ДЕНЬ ЧТОБ НЕ СОВПАДАЛИ НОМЕРА ЧЕКОВ И ПОСЛЕДОВАТЕЛЬНОСТЬ ВРЕМЕНИ И НОМЕРА БЫЛИ ВЕРНЫ=============!!!!!!!!!!!!!!!!!!!!!!!!===============================--------------------
       function checkNumItem(time) {
         //checkNum номер чека
         //averageNumCheck среднее количество в час
         console.log(time)

         if(time>1000 && time<1100) {
          checkNum=randomInteger(1, averageNumCheck);
         }

         if(time>1059 && time<1200) {
          checkNum=randomInteger(averageNumCheck, averageNumCheck*2);
         }

         if(time>1159 && time<1300) {
          checkNum=randomInteger(averageNumCheck*2+1, averageNumCheck*3);
         }


         if(time>1259 && time<1400) {
          checkNum=randomInteger(averageNumCheck*3+1, averageNumCheck*4);
         }


         if(time>1359 && time<1500) {
          checkNum=randomInteger(averageNumCheck*4+1, averageNumCheck*5);
         }


         if(time>1459 && time<1600) {
          checkNum=randomInteger(averageNumCheck*5+1, averageNumCheck*6);
         }

         if(time>1559 && time<1700) {
          checkNum=randomInteger(averageNumCheck*6+1, averageNumCheck*7);
         }

         if(time>1659 && time<1800) {
          checkNum=randomInteger(averageNumCheck*7+1, averageNumCheck*8);
         }
     
       }


