       //Формируем 48 элементов в строку
   function symbolString(el) {
       let addressArr = el.innerText.split('');
       
       if (addressArr.length> 48) {
           let newArr1 = addressArr.slice(0,48).join('');
           addressArr.splice(0,48);
           let newArr2 = addressArr.join('');
           el.innerHTML = newArr1;
           let addressBlock = document.createElement('div');
           addressBlock.innerHTML = newArr2;
           el.append(addressBlock);
       };
   };
