import React, { Component } from 'react';

import {
  Route,
  Switch,
  Redirect,
  withRouter
} from "react-router-dom"



import './App.css';
import ChecksView from './containers/ChecksView/ChecksView';
import InitialData from './blocks/initialData';


class App extends Component {
  
  render() {
    const { history } = this.props
    
    return (
      <div className="App">
      
      

       <Switch>
       
            <Route history={history} path='/checksview' component={ChecksView} />
            <Route history={history} path='/initialdata' component={InitialData} />
           
            <Redirect from='/' to='/initialdata'/>
          </Switch>



      </div>
    );
  }
}

export default withRouter(App);